'use client';

import { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Badge } from '@/components/ui/Badge';
import { Download, FileText } from 'lucide-react';
import { mockDepartmentPerformance, mockInstructorRankings, mockEvaluationResponses, mockCourses } from '@/data/mock';

const generateCSVReport = (filename: string, data: any[], headers: string[]) => {
  try {
    const csv = [
      headers.join(','),
      ...data.map(d => headers.map(h => `"${String((d as any)[h] ?? '')}"`).join(',')),
    ].join('\n');
    const blob = new Blob([csv], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = filename;
    document.body.appendChild(a);
    a.click();
    a.remove();
    URL.revokeObjectURL(url);
  } catch (e) {
    alert('Failed to generate report');
  }
};

const ReportsComponent = () => {
  const [generating, setGenerating] = useState<string | null>(null);

  const handleDepartmentReport = async () => {
    setGenerating('department');
    await new Promise(r => setTimeout(r, 500)); // simulate processing
    generateCSVReport(
      `department-report-${new Date().toISOString().split('T')[0]}.csv`,
      mockDepartmentPerformance,
      ['period', 'score', 'completionRate']
    );
    setGenerating(null);
  };

  const handleInstructorReport = async () => {
    setGenerating('instructor');
    await new Promise(r => setTimeout(r, 500));
    generateCSVReport(
      `instructor-report-${new Date().toISOString().split('T')[0]}.csv`,
      mockInstructorRankings.map(r => ({
        name: r.instructor?.name,
        overallScore: r.overallScore,
        yearsOfService: r.yearsOfService,
        department: r.department,
      })),
      ['name', 'overallScore', 'yearsOfService', 'department']
    );
    setGenerating(null);
  };

  const handleCourseReport = async () => {
    setGenerating('course');
    await new Promise(r => setTimeout(r, 500));
    const courseData = mockCourses.map(c => ({
      code: c.code,
      name: c.name,
      section: c.section,
      credits: c.credits,
    }));
    generateCSVReport(
      `course-report-${new Date().toISOString().split('T')[0]}.csv`,
      courseData,
      ['code', 'name', 'section', 'credits']
    );
    setGenerating(null);
  };

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-gray-900 dark:text-white">Reports</h1>
        <p className="text-gray-600 dark:text-gray-400 mt-2">
          Generate and download evaluation reports
        </p>
      </div>

      {/* Report Types */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Department Report</CardTitle>
            <CardDescription>Overall department performance summary</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <p className="text-sm text-gray-600 dark:text-gray-400">
                Includes: Performance trends, top performers, improvement areas
              </p>
            </div>
            <Button variant="primary" className="w-full" onClick={handleDepartmentReport} disabled={generating === 'department'}>
              <Download className="w-4 h-4 mr-2" />
              {generating === 'department' ? 'Generating...' : 'Generate Report'}
            </Button>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Instructor Report</CardTitle>
            <CardDescription>Individual teacher evaluation summary</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <p className="text-sm text-gray-600 dark:text-gray-400">
                Includes: Ratings, strengths, areas for improvement
              </p>
            </div>
            <Button variant="primary" className="w-full" onClick={handleInstructorReport} disabled={generating === 'instructor'}>
              <Download className="w-4 h-4 mr-2" />
              {generating === 'instructor' ? 'Generating...' : 'Generate Report'}
            </Button>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Course Report</CardTitle>
            <CardDescription>Course-level evaluation analysis</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <p className="text-sm text-gray-600 dark:text-gray-400">
                Includes: Class feedback, engagement metrics
              </p>
            </div>
            <Button variant="primary" className="w-full" onClick={handleCourseReport} disabled={generating === 'course'}>
              <Download className="w-4 h-4 mr-2" />
              {generating === 'course' ? 'Generating...' : 'Generate Report'}
            </Button>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Analytics Dashboard</CardTitle>
            <CardDescription>Interactive evaluation analytics</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <p className="text-sm text-gray-600 dark:text-gray-400">
                View charts, trends, and detailed breakdowns
              </p>
            </div>
            <Button variant="primary" className="w-full">
              <FileText className="w-4 h-4 mr-2" />
              Open Dashboard
            </Button>
          </CardContent>
        </Card>
      </div>

      {/* Recent Reports */}
      <Card>
        <CardHeader>
          <CardTitle>Recent Reports</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-2">
            <div className="flex justify-between items-center p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
              <div>
                <p className="font-semibold text-gray-900 dark:text-white">Department Report S1 2024</p>
                <p className="text-sm text-gray-600 dark:text-gray-400">Generated 2 days ago</p>
              </div>
              <Button variant="outline" size="sm">
                <Download className="w-4 h-4" />
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default ReportsComponent;
